* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    font-family: sans-serif;
    background: #f1f2f6;
}
li {
    list-style: none;
}
.container {
    width: 100%;
    max-width: 1340px;
    margin: 0 auto;
    padding: 0 30px;
}

.blackout{    
    transition: background-color .2s linear;
}
.blackout--active{
    background-color: rgba(61, 61, 61, 0.3);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
.locked{
    overflow: hidden;
}

.header {
    padding: 50px 0;

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
nav {
    width: 100%;
}
.menu-btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    z-index: 20;
    transition: all 0.5s ease-in-out;
}
.menu-btn__item {
    width: 100%;
    height: 5px;
    background: #b132db;
    border-radius: 5px;
    transition: all 0.5s ease-in-out;
}
.menu-btn__item::before,
.menu-btn__item::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 5px;
    background: #b132db;
    border-radius: 5px;
    transition: all 0.5s ease-in-out;
}
.menu-btn__item::before {
    transform: translateY(-16px);
}
.menu-btn__item::after {
    transform: translateY(16px);
}

.menu-btn.open .menu-btn__item {
    transform: translateX(-50px);
    background: transparent;
    box-shadow: none;
}
.menu-btn.open .menu-btn__item::before {
    transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn__item::after {
    transform: rotate(-45deg) translate(35px, 35px);
}

.menu {
    position: relative;

    &__list {
        position: absolute;
        top: 70px;
        left: -100%;
        z-index: 11;
        transform: translateX(-100%);
        transition: left .5s linear, transform .5s linear;
    }

    &__item {
        margin: 15px 0;
        font-size: 34px;
        font-weight: 700;
        color: #fff;
        letter-spacing: 1px;
        text-transform: uppercase;
        opacity: 0.8;
        position: relative;
        cursor: pointer;
        transition: opacity .1s linear;

        &:first-child,
        &:last-child {
            margin: 0;
        }

        &:hover {
            opacity: 1;
        }
    }
}
.menu__list.open {
    left: 0;
    transform: translateX(0);
}

.menu-bg {
    position: absolute;
    top: 0;
    left: -100%;
    width: 50%;
    height: 100%;
    background-color: #f39c12;
    border-radius: 0 20% 60% 0;
    transition: left 0.5s linear;
    z-index: 10;
}
.menu-bg--play{
    background-color: #2ecc71;
}
.menu-bg.open {
    left: 0;
}

.switch {
    position: relative;
    display: inline;

    & span {
        position: absolute;
        bottom: 15px;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        color: #fff;
    }
}

.checkbox{
    position: relative;
    width: 120px;
    height: 40px;
    background: #f39c12;
    -webkit-appearance: none;
    border-radius: 25px;
    outline: none;
    transition: 0.4s;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.checkbox:checked {
    background: #2ecc71;
}

.checkbox:before {
    content: '';
    width: 40px;
    height: 40px;
    position: absolute;
    right: 0;
    background: #fff;
    border-radius: 50%;
    transform: scale(0.9);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: 0.4s;
}

.checkbox:checked:before {
    right: 80px;
}
.train {
    left: 15px;
    z-index: 11;
}

.play {
    right: 15px;
    z-index: 11;
}
.none {
    display: none;
}

//content

.content {
    &__title {
        font-size: 50px;
        font-weight: 700;
        color: #646060;
        margin-bottom: 30px;
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
}

.category {
    width: calc(100% / 4 - 30px);
    height: 290px;
    background: #e7e4e4;
    cursor: pointer;
    border-radius: 20px;
    margin-bottom: 40px;
    transition: all .2s linear;

    &:hover{
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.25);
    }

    &__img {
        margin-bottom: 20px;

        & img {
            display: block;
            width: 100%;
            height: 200px;
            object-fit: cover;
            border-radius: 20px 20px 0 0;
        }
    }

    &__title {
        font-size: 20px;
        font-weight: 700;
        color: #646060;
        padding-left: 20px;
        margin-bottom: 10px;
    }

    &__info{
        font-size: 12px;
        padding: 0px 40px 0px 20px;
        position: relative;

        &:after{
            content: '';
            display: block;
            width: 20px;
            height: 20px;
            background-color: #f39c12;
            border-radius: 50%;
            position: absolute;
            top: 50%;
            right: 20px;
            transform: translateY(-50%);
        }
    }
}

.category__info.info--play:after{
    background-color:#2ecc71;
}







//media




@media (max-width: 1199px){
    .category{
        width: calc(100% / 3 - 25px)
    }
}



@media (max-width: 991px){
    .header{
        padding: 40px 0;
    }
    .content__title{
        font-size: 40px;
    }
    .category{
        width: calc(100% / 2 - 25px)
    }
}



@media (max-width: 767px){
    .container{
        max-width: 1310px;
        padding: 0 15px;
    }
    .header{
        padding: 30px 0;
    }
    .checkbox{
        width: 100px;
    }
    .checkbox:checked:before {
        right: 60px;
    }
    .menu{
        &__list{
            top: 50px;
        }
        &__item{
            font-size: 30px;
        }
    }
    .content__title{
        font-size: 30px;
    }
    .category{
        width: 100%
    }
    .menu-bg{
        width: 100%;
        border-radius: 0;
    }
}